.quicktab-dropdown-menu {
  display: inline-block;
  width: 9rem;
  padding: 0;
  margin: 0;
  overflow: hidden;
  list-style: none;
  border-radius: 0.375rem;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
}
.quicktab-dropdown-menu li {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  font-weight: 400;
  color: #212529;
  cursor: pointer;
  user-select: none;
}
.quicktab-dropdown-menu li:hover, .quicktab-dropdown-menu li:focus {
  color: #212529;
  background-color: #f8f9fa;
}
.quicktab-dropdown-menu li.active, .quicktab-dropdown-menu li:active {
  color: #fff;
  background-color: #7431fa;
}
.quicktab-dropdown-menu li > span {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}